import React, {Component} from "react";
import axios from "axios";

import {Chart, Axis, Tooltip, Geom} from "bizcharts";


const baseUrl = 'http://120.79.229.14:8888';

export default class IndexAdmin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tables: [],
        };
    }

    componentDidMount() {
        //查桌子有无坐下
        this.fetchAllSite();
        console.log(this.state)


    }

    fetchAllSite = () => {
        axios.get(
            baseUrl + '/admin/student',
            {
                params: {
                    // token: localStorage.customer_token,
                }
            })
            .then((r) => {
                if (r.data.code === 100) {
                    this.setState({
                        students: r.data.extend.students,
                    })
                }
            })
            .catch((err) => {
                console.log(err);
            })
    };

    render() {
        const students = this.state.students;
        return (
            <div>
                <Chart height={400} data={students} forceFit>
                    {/*<Axis name="month" />*/}
                    <Axis name="studentName" />
                    <Axis name="temperature" label={{formatter: val => `${val}°C`}} />
                    <Tooltip crosshairs={{type : 'y'}} />
                    <Geom type="line" position="month*temperature" size={2} color={'city'} />
                    <Geom type="point" position="month*temperature" size={4} color={'city'} />
                </Chart>
            </div>
        )
    }
}

