import {configure, observable, computed, action, runInAction} from 'mobx';
import axios from 'axios'
import {baseUrl} from "../../config";
// configure({
//     enforceActions: 'observed'
// });

class Store {
    @observable chats;
    @observable chatList = [];
    @observable chatListA = [];
    @observable Avatar = [];
    @observable payImg = '';
    @observable showBtnFunctions = false;
    @observable visibleModalRecvQR = false;
    @observable visibleModalRecvQRUpload = false;
    @observable showBtnFunctions = false;

    @computed get getChats() {
        return this.chats;
    }

    @computed get getPayImg() {
        return this.payImg;
    }

    @computed get getChatListA() {
        return this.chatListA;
    }

    @computed get getAvatar() {
        return this.Avatar;
    }

    @computed get getChatList() {
        const all_id_to = [];
        if (this.chatList.from) {
            this.chatList.from.map(i => {
                if (i.starter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.starter)
                }
                if (i.geter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.geter)
                }
            });
            this.chatList.to.map(i => {
                if (i.starter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.starter)
                }
                if (i.geter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.geter)
                }
            });
        }
        const s = new Set();
        all_id_to.forEach(x => s.add(x));
        const unsame = Array.from(s);//不重复数组
        // 获取头像
        const arr = unsame;
        const list = {};
        for (var key in arr) {
            list[key] = `${arr[key]}`;
        }
        console.log(list);
        axios.get(
            baseUrl + '/api/get_user_name',
            {
                params: {
                    'token': localStorage.customer_token,//*
                    'infs': JSON.stringify(list),
                }
            })
            .then((r) => {
                this.setChatListWithAvatar(r.data)
            });
        return this.chatListA;
    }

    @action setChats = (v) => {
        console.log(v);
        this.chats = v;
    };
    @action setPayImg = (v) => {
        this.payImg = v;
    };

    @action setChatList = (v) => {
        this.chatList = v;
    };
    @action setChatListWithAvatar = (v) => {
        this.chatListA = v;
    };
    @action setChatingAvatar = (v) => {
        this.Avatar = v;
    };
    @action tggModal = () => {
        this.visibleModalRecvQR = !this.visibleModalRecvQR;
    };
    @action tgglBtnFs = () => {
        this.showBtnFunctions = !this.showBtnFunctions;
    };
    @action tggModalUpload = () => {
        console.log('click')
        this.visibleModalRecvQRUpload = !this.visibleModalRecvQRUpload;
    };
    @action
    async fetchChats() {
        await axios.get(
            baseUrl + '/api/get_websocket_message',
            {
                params: {
                    'token': localStorage.customer_token,
                    'id_to': localStorage.chat_id_to,
                }
            }
        ).then((r) => {
            console.log("in axios ", r.data);
            if (r.data !== 'undefined') {
                this.setChats(r.data);
            }
        }).catch((err) => {
            console.log("in axios ", err)
        });
    }

    @action
    async fetchPayImg() {
        await axios.get(
            baseUrl + '/api/get_pay_img',
            {
                params: {
                    'token': localStorage.customer_token,
                    'user_id': localStorage.id_user,
                }
            }
        ).then((r) => {
            console.log("in axios ", r.data);
            this.setPayImg(r.data);
        }).catch((err) => {
            console.log("in axios ", err)
        });
    }

    @action
    async fetchChatList() {
        await axios.get(
            baseUrl + '/api/get_list_of_websocket_message',
            {
                params: {
                    'token': localStorage.customer_token,
                    'limit': 50,
                }
            }
        ).then((r) => {
            console.log("in axios ", r.data);
            // this.chatList = r.data;

            this.setChatList(r.data);
        }).catch((err) => {
            console.log("in axios ", err)
        });
        const all_id_to = [];
        if (this.chatList.from) {
            this.chatList.from.map(i => {
                if (i.starter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.starter)
                }
                if (i.geter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.geter)
                }
            });
            this.chatList.to.map(i => {
                if (i.starter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.starter)
                }
                if (i.geter !== parseInt(localStorage.id_user)) {
                    all_id_to.push(i.geter)
                }
            });
        }
        const s = new Set();
        all_id_to.forEach(x => s.add(x));
        const unsame = Array.from(s);//不重复数组
        // 获取头像
        const arr = unsame;
        const list = {};
        for (var key in arr) {
            list[key] = `${arr[key]}`;
        }
        console.log(list);
        axios.get(
            baseUrl + '/api/get_user_name',
            {
                params: {
                    'token': localStorage.customer_token,//*
                    'infs': JSON.stringify(list),
                }
            })
            .then((r) => {
                this.setChatListWithAvatar(r.data)
            });
    }

    @action
    async fetchAvatars() {
        const arr = [
            localStorage.id_user,
            localStorage.chat_id_to,
        ];
        const list = {};
        for (var key in arr) {
            list[key] = `${arr[key]}`;
        }
        console.log(list);
        axios.get(
            baseUrl + '/api/get_user_name',
            {
                params: {
                    'token': localStorage.customer_token,//*
                    'infs': JSON.stringify(list),
                }
            })
            .then((r) => {
                this.setChatingAvatar(r.data)
            });
    }
}

export default new Store();