    fetchAllSite = () => {
        axios.get(
            baseUrl + '/table/',
            {
                params: {
                    // token: localStorage.customer_token,
                }
            })
            .then((r) => {
                if (r.data.code === 100) {

                    table_d["213"].map(i => {
                        r.data.extend.tables.map(t => {
                            if (i.tableId === t.tableId) {
                                i.state = t.state;
                            }
                        })
                    });
                    table_d["214"].map(i => {
                        r.data.extend.tables.map(t => {
                            if (i.tableId === t.tableId) {
                                i.state = t.state;
                            }
                        })
                    });
                    table_d["215"].map(i => {
                        r.data.extend.tables.map(t => {
                            if (i.tableId === t.tableId) {
                                i.state = t.state;
                            }
                        })
                    });
                    console.log(table_d["213"]);
                    console.log(table_d["214"]);
                    console.log(table_d["215"]);
                    this.setState({
                        tables: r.data.extend.tables,
                    })
                }
            })
            .catch((err) => {
                console.log(err);
            })
    };

