import React from "react";
import {baseUrl} from "../../../config";
import {Avatar, Input} from "antd";
import './index.less'
import store from "../store";
import {observer} from "mobx-react";



export default @observer
class SkillChatIndex extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    };

    componentDidMount() {
        store.fetchChatList();

    }

    jump2chat = (id) => {
        localStorage.chat_id_to = id;
        window.location.hash = `#/skillChat`

    };

    render() {
        return (
            <div
                className='chatlist-container'
            >
                {
                    store.getChatListA
                        ?
                        store.getChatListA.map(i => {
                            return (
                                <div
                                    className='chatlist-cell'
                                    onClick={this.jump2chat.bind(this, i.id)}
                                >
                                    <span className='chat-avatar'>
                                        <Avatar size={40} shape='square'
                                                src={`${baseUrl}/${i.avatarUrl}`}
                                                className='avatar'>头像</Avatar>
                                    </span>
                                    <span className='chat-to-name'>
                                        {i.nickName}
                                    </span>
                                </div>
                            )
                        })
                        : null
                }
            </div>
        )
    }
}