import React from "react";
import axios from "axios";
import {baseUrl} from "../../../config";
import {Avatar, Button, Icon, Modal, Input, Spin, message} from "antd";
import { withRouter } from "react-router-dom";
import wx from "weixin-js-sdk";
import './index.less'
import moment from "moment";

const {Search} = Input;

class ContentSkill extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            collected: false,
            liked: false,
            zan_count: 0,
            showLeaveMessageInput: false,
            userInfo: [],
            id_to: 0,
        };
        this.fetchJineng();
        // this.fetchMyCollects();
        // this.share();

    };

    componentDidMount() {

    }

    fetchJineng = () => {
        axios.get(
            baseUrl + '/api/get_jineng',
            {
                params: {
                    'id_jineng': this.props.match.params.id,
                    'token': localStorage.customer_token,//*
                    'id_to': localStorage.now_id_to,//*

                }
            })
            .then((r) => {
                console.log(r.data);
                this.setState({
                    dataSource: r.data,
                    liked: r.data.has_zan !== 0,
                    zan_count: r.data.zan_count !== undefined ? r.data.zan_count : 0,
                });
                if (r.data.collected.length !== 0) {
                    this.setState({
                        collected: true,
                    });
                }
                this.share();
                const humans = [];
                if (r.data.pin_lun) {

                    r.data.pin_lun.map(i => {
                        humans.push(i.id_from);
                        humans.push(i.id_to);
                    });
                }
                const set_human = [...new Set(humans)];
                console.log(set_human);
                this.fetchUserInfo(set_human);
            })
    };
    fetchUserInfo = (s) => {
        const arr = s;
        const list = {};
        for (var key in arr) {
            list[key] = `${arr[key]}`;
        }
        console.log(list);
        axios.get(
            baseUrl + '/api/get_user_name',
            {
                params: {
                    'token': localStorage.customer_token,//*
                    'infs': JSON.stringify(list),
                }
            })
            .then((r) => {
                this.setState({
                    userInfo: r.data,
                })
            });
    };
    fetchMyCollects = () => {
        axios.get(
            baseUrl + '/api/get_collected_jineng',
            {
                params: {
                    'token': localStorage.customer_token,//*
                }
            })
            .then((r) => {
                console.log(r.data);
                const thisId = this.props.match.params.id;
                r.data.jineng.map(i => {
                    console.log(i.id, thisId);
                    if (i.id === parseInt(thisId)) {
                        this.setState({
                            collected: true,
                        });
                    }
                });
                // this.share();
            })
    };
    filterUserInfo = (id) => {
        let u = {};
        this.state.userInfo.map(i => {
            if (i.id === id) {
                console.log(id, i);
                u = i;
            } else return {}
        });
        console.log('返回的u', u);
        return u
    };
    share = () => {
        const d = this.state.dataSource;
        console.log(d, this.state);
        const convertTpl = (id) => {
            const d = {
                1: '快递代取',
                2: '电费代充',
                3: '电影票代买',
                4: '跑腿',
                5: '账号出租',
                6: '课程服务',
                7: '拍照',
                8: '修图',
                9: '化妆',
                10: '游戏陪玩',
                11: '电车出租',
                12: '司机',
                13: '服务员',
                14: '礼仪',
                15: '模特',
                16: '配音',
                17: '打印',
                18: '住宿',
                19: '商家',
                20: '微信群',
                21: '海大公众号',
                22: '二手',
                23: '招商',

            };
            return d[id]
        };
        axios.get(baseUrl + '/api/get_jssdk',
            {
                params: {
                    url: window.location.href.split('#')[0]
                }
            }).then(res => {
            const params = {
                debug: false,
                appId: res.data.appId,
                timestamp: res.data.timestamp,
                nonceStr: res.data.nonceStr,
                signature: res.data.signature,
                jsApiList: [
                    'onMenuShareTimeline',
                    'onMenuShareAppMessage'
                ]
            };
            wx.config(params);
            wx.ready(function () {
                // config信息验证成功后会执行ready方法
                //通过ready接口处理成功验证
                wx.onMenuShareAppMessage({
                    // 分享给朋友
                    title: `【小蓝来帮忙】${d.jineng[0].bei_zhu}`, // 分享标题
                    desc: `${convertTpl(d.jineng[0].jineng_tpl)}达人`,
                    link: res.data.str, // 分享链接
                    imgUrl:
                        `${baseUrl}/storage/1v2F1byxddM3FGWFfCNQxmRGcw8DMTTCH0Q0rRHz.jpeg`, // 分享图标
                    type: "", // 分享类型,music、video或link，不填默认为link
                    dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
                wx.onMenuShareTimeline({
                    //分享朋友圈
                    title: `【小蓝来帮忙】${d.jineng[0].bei_zhu}`, // 分享标题
                    desc: `${convertTpl(d.jineng[0].jineng_tpl)}达人`,
                    link: res.data.str, // 分享链接
                    imgUrl:
                        `${baseUrl}/storage/1v2F1byxddM3FGWFfCNQxmRGcw8DMTTCH0Q0rRHz.jpeg`, // 分享图标
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
            });
        });
    };
    convertTpl = (id) => {
        const d = {
            1: '快递代取',
            2: '电费代充',
            3: '电影票代买',
            4: '跑腿',
            5: '账号出租',
            6: '课程服务',
            7: '拍照',
            8: '修图',
            9: '化妆',
            10: '游戏陪玩',
            11: '电车出租',
            12: '司机',
            13: '服务员',
            14: '礼仪',
            15: '模特',
            16: '配音',
            17: '打印',
            18: '住宿',
            19: '商家',
            20: '微信群',
            21: '海大公众号',
            22: '二手',
            23: '招商',

        };
        return d[id]
    };
    goBack = () => {
        document.location.hash = '#/skill'

    };
    // 联系方式弹窗
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    showCommunications = (id, receiver_id) => {
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    token: localStorage.customer_token,
                    method: 'solo_get',
                    id_communication: id,
                }
            })
            .then((r) => {
                this.setState({
                    visibleCommunications: true,
                    modalContent: <div>
                        <div>
                            <h2 className='name'>联系方式:{r.data.communications[0].phone}</h2>
                            <img src={`${baseUrl}/${r.data.communications[0].wx_png}`} alt=''
                                 className='avatar'
                                 width='100%'
                            />
                            <h5>提示：长按识别二维码添加微信</h5>
                        </div>
                    </div>,
                });
            });
        axios.get(
            baseUrl + '/api/sent_message',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_to: receiver_id,
                    content: `${localStorage.nick_name}正在找您`,
                }
            })
            .then((r) => {
                console.log(r.data);
            })
    };
    //收藏
    favoriteHandler = (id, receiver_id, skill_name) => {
        axios.get(
            baseUrl + '/api/collect_jineng',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_jineng: id,  //货号
                    method: this.state.collected ? 'cancle' : 'none',    //取消赞
                    id_to: receiver_id,
                    nick_name: localStorage.nick_name,

                }
            })
            .then((Response) => {
                console.log('收藏后', Response);
                this.setState(prevState => ({
                        collected: !(prevState.collected),
                    })
                )
                // if (Response.data === 'success' || Response.data === 1) {
                //     // message.success('收藏成功');
                //     this.setState(prevState => ({
                //             collected: !(prevState.collected),
                //         })
                //     )
                // } else {
                //     message.error('失败，请稍后重试');
                // }
            });
        if (!this.state.collected) {
            axios.get(
                baseUrl + '/api/sent_message',
                {
                    params: {
                        token: localStorage.customer_token, //用户token
                        id_to: receiver_id,
                        content: `${localStorage.nick_name}收藏了您的${skill_name}`,
                    }
                })
                .then((r) => {
                    console.log(r.data);
                })
        }

    };
    // 点赞
    likeHandler = (id, receiver_id, skill_name) => {
        const p = {
            token: localStorage.customer_token, //用户token
            id_jineng: id,  //货号
            id_to: receiver_id,
            nick_name: localStorage.nick_name,

        };
        if (this.state.liked) {
            p.method = 'cancle'
        }
        axios.get(
            baseUrl + '/api/zan_jineng',
            {
                params: p
            })
            .then((Response) => {
                console.log('收藏后', Response);
                this.setState(prevState => ({
                        liked: !(prevState.liked),
                    })
                );
                // if (Response.data === 1) {
                //     this.setState({
                //         liked: false,
                //     })
                // }
                // if (Response.data === 'success') {
                //     // message.success('成功');
                //     this.setState(prevState => ({
                //             liked: !(prevState.liked),
                //         })
                //     )
                // } else {
                //     // message.error('失败');
                // }
            });
        axios.get(
            baseUrl + '/api/sent_message',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_to: receiver_id,
                    content: `${localStorage.nick_name}赞了您的${skill_name}`,
                }
            })
            .then((r) => {
                console.log(r.data);
            })
    };
    toggleLeaveMessage = (id) => {
        console.log(id);
        this.setState(prevState => ({
                showLeaveMessageInput: !(prevState.showLeaveMessageInput),
                id_to: id,
            })
        );
    };
    handleMessagesInput = (id_to, v) => {
        axios.get(
            baseUrl + '/api/pin_lun',
            {
                params: {
                    token: localStorage.customer_token,
                    id_jineng: this.props.match.params.id,
                    id_to: this.state.id_to,
                    content: v,

                }
            })
            .then((r) => {
                if (r.data === 1) {
                    message.success('留言成功');
                    this.setState(prevState => ({
                            showLeaveMessageInput: !(prevState.showLeaveMessageInput),
                        })
                    );
                    this.fetchJineng();

                } else {
                    message.error('留言失败，请稍后重试');
                }
            })

    };
    jump2Chat = (id_to) => {
        localStorage.chat_id_to = id_to;
        this.props.history.push("/skillChat");
    };
    render() {
        const d = this.state.dataSource;
        const fs = [];
        if (d && d.jineng_files.length !== 0) {
            d.jineng_files.map(i => {
                fs.push(`${baseUrl}/${i.content}`)
            });
        }
        return (
            <div className='content-skill'>
                <div className='back-to-last'>
                    <span onClick={this.goBack}>
                        <Icon type="left"/>
                        <span>返回</span>
                    </span>
                </div>
                {
                    d !== undefined ?
                        <div>
                            <div className='poster-info'>
                                <div className='avatar-name'>
                                    <Avatar size={40} shape='square'
                                            src={`${baseUrl}/${d.senter_info[0].avatarUrl}`}
                                            className='avatar'>头像</Avatar>
                                    <span className='name'>{d.senter_info[0].nickName}</span>
                                    <span className='find-him'>
                                        <Button
                                            onClick={this.showCommunications.bind(this, d.jineng[0].id_communication)}
                                        >
                                            找ta
                                        </Button>
                                    </span>

                                </div>
                            </div>
                            <div className='skill-main'>
                                <div className='skill-info'>
                                    {d.jineng[0].bei_zhu}
                                </div>
                                <div className='skill-img'>
                                    {d.jineng_files.length !== 0 ?
                                        d.jineng_files.map(i => {
                                            return <img
                                                className='skill-img'
                                                src={`${baseUrl}/${i.content}`}
                                                alt=''
                                                onClick={() => {
                                                    const WeixinJSBridge = window.WeixinJSBridge;
                                                    WeixinJSBridge.invoke("imagePreview", {
                                                        "urls": fs,
                                                        "current": `${baseUrl}/${i.content}`
                                                    });
                                                }}
                                            />
                                        })
                                        : null
                                    }
                                    <div className='explain-info'>
                                        免费代发布兼职，技能，商家信息
                                        <br/>
                                        有需要请联系平台客服 15768860138（微信同号）
                                    </div>
                                    <div className='pinlun-container'>
                                        <h3>全部留言</h3>
                                        {
                                            d.pin_lun ?
                                                d.pin_lun.map(i => {
                                                    return (
                                                        <div
                                                            className='pinlun-cell'
                                                            onClick={this.toggleLeaveMessage.bind(this, i.id_from)}
                                                        >
                                                            <Avatar size={30} shape='square'
                                                                    src={`${baseUrl}/${this.filterUserInfo(i.id_from).avatarUrl}`}
                                                                    className='avatar'>
                                                                {this.filterUserInfo(i.id_from).nickName}
                                                            </Avatar>
                                                            {/*{this.filterUserInfo(i.id_from).nickName}*/}

                                                            回复
                                                            <Avatar size={30} shape='square'
                                                                    src={`${baseUrl}/${this.filterUserInfo(i.id_to).avatarUrl}`}
                                                                    className='avatar'>
                                                                {this.filterUserInfo(i.id_to).nickName}
                                                            </Avatar>
                                                            {/*{this.filterUserInfo(i.id_to).nickName}*/}

                                                            :
                                                            <div>
                                                                <h3>
                                                                    {i.content}
                                                                </h3>
                                                            </div>
                                                            <div className='pinlun-time'>
                                                                {moment.unix(i.created_at).format("MM-DD HH:mm")}
                                                            </div>
                                                        </div>
                                                    )
                                                })
                                                : null
                                        }
                                    </div>
                                    <div className='footer-skill'>
                                        {
                                            this.state.showLeaveMessageInput
                                                ?
                                                <div
                                                    className='message-input'
                                                >
                                                    <div className='toggle-down'>
                                                        <Icon
                                                            type="down"
                                                            onClick={this.toggleLeaveMessage}
                                                        />
                                                    </div>
                                                    <Search
                                                        placeholder={`回复：${this.filterUserInfo(this.state.id_to).nickName}`}
                                                        enterButton="留言"
                                                        size="large"
                                                        onSearch={this.handleMessagesInput.bind(this, d.senter_info[0].id)}
                                                    />
                                                </div>
                                                : <div className='default-footer'>
                                                    <div className='icon-favorite'
                                                         onClick={this.favoriteHandler.bind(this, d.jineng[0].id, d.senter_info[0].id, d.jineng[0].bei_zhu)}>
                                                        <Icon type="star"
                                                              theme={this.state.collected ? 'filled' : ''}
                                                              style={{fontSize: '1.5em', color: 'pink'}}
                                                        />
                                                        收藏
                                                    </div>
                                                    <div className='icon-favorite'
                                                         onClick={this.likeHandler.bind(this, d.jineng[0].id, d.senter_info[0].id, d.jineng[0].bei_zhu)}>
                                                        <Icon type="like"
                                                              theme={this.state.liked ? 'filled' : ''}
                                                              style={{fontSize: '1.5em', color: '#71ceff'}}
                                                        />
                                                        超赞
                                                        {this.state.zan_count !== 0 ? this.state.zan_count : ''}
                                                    </div>
                                                    <div className='icon-favorite'
                                                         onClick={this.toggleLeaveMessage.bind(this, d.senter_info[0].id)}>
                                                        <Icon type="message"
                                                              style={{fontSize: '1.5em', color: '#71ceff'}}
                                                        />
                                                        留言
                                                    </div>
                                                    {/*<div className='icon-favorite right'>*/}

                                                    {/*    <Button*/}
                                                    {/*        onClick={this.jump2Chat.bind(this,d.senter_info[0].id)}*/}
                                                    {/*    >*/}
                                                    {/*        跟ta聊聊*/}
                                                    {/*    </Button>*/}
                                                    {/*</div>*/}
                                                </div>
                                        }
                                    </div>
                                </div>

                            </div>
                        </div>
                        : <Spin/>

                }
                <Modal
                    visible={this.state.visibleCommunications}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    {this.state.modalContent}
                </Modal>
            </div>
        )
    }
}
export default withRouter(ContentSkill)
