import React from "react";
import axios from "axios";
import {baseUrl} from "../../../config";
import {Avatar, Icon, Input, message, Modal} from "antd";
import './index.less'
import {ChooseRecvQR} from "./ChooseRecvQR";
import store from "../store";
import {observer} from "mobx-react";
import {UploadImgChat} from "./UploadImgChat";

const {Search} = Input;


export default @observer
class SkillChat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // showBtnFunctions: false,
            // visibleModalRecvQR: false,
            modalContent: 1,

        };

    };

    componentDidMount() {
        // 获取当前头像
        store.fetchAvatars();
        store.fetchChats();
    }

    tgglBtnFs = () => {
        this.setState(prevState => ({
                showBtnFunctions: !(prevState.showBtnFunctions),
            })
        )
    };
    hdlInput = (v) => {
        if (v.length === 0) {
            return 0;
        }
        const a = {
            'content': v
        };
        axios.get(
            baseUrl + '/api/sent_websocket_message',
            {
                params: {
                    token: localStorage.customer_token,
                    id_to: localStorage.chat_id_to,
                    content: JSON.stringify(a),
                }
            })
            .then((r) => {
                if (r.data === 1) {
                    message.success('发送成功');
                    this.setState(prevState => ({
                            showLeaveMessageInput: !(prevState.showLeaveMessageInput),
                        })
                    );
                    store.fetchChats();
                    this.refs.searchBar.input.state.value = '';

                } else {
                    message.error('发送失败，请稍后重试');
                }
            })

    };
    showModalRecvQR = () => {
        this.setState({
            visibleModalRecvQR: true,

        })

    };
    renderChat = (chs) => {
        const cs = chs;
        const csv = [];
        // 构建头像查询
        const avts = [];
        for (const c in cs) {
            console.log(c, ":", cs[c]);
            const userId = Object.keys(cs[c])[0]; //当前用户
            const chatContent = cs[c][Object.keys(cs[c])[0]];
            // const userAvatar = this.fetchUserInfo();
            avts.push(userId);
            csv.push(
                <div>
                    {userId}:{chatContent}@{c}
                </div>
            )
        }
        // this.fetchUserInfo(avts);
        return csv
    };
    handleCancel = () => this.setState({visibleModalRecvQR: false});
    goBack = () => {
        window.history.go(-1);
    };
    parseMessage = (m) => {
        try {
            const a = JSON.parse(m.replace(/&quot;/g, '"'));
            console.log(m, a);
            // console.log(a.img)
            return a.img
                ? <img
                src={a.img}
                alt=''
                className='message-img'
                onClick={
                    ()=>{
                        const WeixinJSBridge = window.WeixinJSBridge;
                        WeixinJSBridge.invoke("imagePreview",{
                            "urls":[a.img],
                            "current":a.img,
                        });
                    }
                }
                />
                : a.content
        } catch (e) {
            console.log(e); // error in the above string (in this case, yes)!
        }
    };
    showUploadQR = () => {
        this.setState({
            modalContent: <ChooseRecvQR/>
        });
        store.tggModal();
    };
    showUploadImg = () => {
        this.setState({
            modalContent: <UploadImgChat/>
        });
        store.tggModal();
    };

    render() {
        const myAvatars = (store.Avatar.filter(i => i.id === parseInt(localStorage.id_user)));
        const my = myAvatars[0] ? myAvatars[0].avatarUrl : null;
        const otherAvatar = store.Avatar.filter(i => i.id !== parseInt(localStorage.id_user));
        const other = otherAvatar[0] ? otherAvatar[0].avatarUrl : null;
        const nick_name = otherAvatar[0] ? otherAvatar[0].nickName : null;
        // console.log(other);
        return (
            <div className='chat-main'>
                <div className='back-to-seller' onClick={this.goBack} style={{display: this.state.display_back}}>
                    <span>
                    <Icon type="left"/>
                    返回
                    </span>
                    <span className='middle'>
                        {nick_name}
                    </span>
                </div>
                {
                    store.getChats
                        ? Object.keys(store.getChats).map(i => {
                            // const i_k = Object.keys(i);
                            const i_v = store.getChats[i];
                            const u = Object.keys(i_v);
                            const message = i_v[u];
                            return (
                                <div
                                    className={parseInt(u) === parseInt(localStorage.id_user) ? 'chats-cell right' : 'chats-cell left'}
                                >
                                    {parseInt(u) !== parseInt(localStorage.id_user)
                                        ?
                                        <div>
                                            <span className='avatar-span'>
                                                <Avatar
                                                    size={40} shape='square'
                                                    src={parseInt(u) === parseInt(localStorage.id_user) ? `${baseUrl}/${my}` : `${baseUrl}/${other}`}
                                                    className='avatar'
                                                >
                                                    头像
                                                </Avatar>
                                                {/*{u}:*/}
                                            </span>
                                            <div className='message'>
                                                {this.parseMessage(message)}
                                            </div>
                                        </div>
                                        :
                                        <div>
                                            <div className='message'>
                                                {this.parseMessage(message)}

                                            </div>
                                            <span className='avatar-span'>
                                                <Avatar
                                                    size={40} shape='square'
                                                    src={parseInt(u) === parseInt(localStorage.id_user) ? `${baseUrl}/${my}` : `${baseUrl}/${other}`}
                                                    className='avatar'
                                                >
                                                    头像
                                                </Avatar>
                                            </span>
                                        </div>
                                    }
                                </div>
                            )
                        })
                        : null
                }
                <div className='input-block'>
                    <div className='input-span'>
                        <Search
                            ref="searchBar"
                            placeholder=''
                            enterButton="发送"
                            size="large"
                            onSearch={this.hdlInput}
                        />
                    </div>
                    <div
                        className='input-span-tgl'
                        onClick={store.tgglBtnFs}
                    >
                        <Icon type="plus-circle" style={{fontSize: '2em'}}/>
                    </div>

                    {
                        store.showBtnFunctions
                            ?
                            <div className='btn-functions'>
                                <div
                                    className='funs-cell'
                                    onClick={this.showUploadImg}
                                >
                                    <Icon type="file-image"/>
                                    <div className='small-bottom-font'>
                                        发送图片
                                    </div>
                                </div>
                                <div
                                    className='funs-cell'
                                    onClick={this.showUploadQR}
                                >
                                    <Icon type="qrcode"/>
                                    <div className='small-bottom-font'>
                                    发送收款码
                                    </div>
                                </div>
                            </div>
                            : null
                    }
                </div>

                {/*发送收款码*/}
                <Modal
                    visible={store.visibleModalRecvQR}
                    onCancel={store.tggModal}
                >
                    {this.state.modalContent}
                </Modal>

            </div>
        )
    }
}