import React from 'react'
import './index.less'
import {Button, Form, Icon, Input, message, Modal, notification, Radio, Select, Upload} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import store from '../../store'
import {observer} from "mobx-react";
import {WorkerCmmUpload} from "../../../Work/WorkerCmmIndex/WorkerCmmUpload";
import {UploadRecvQR} from "./UploadRecvQR";
import {Link} from "react-router-dom";

const FormItem = Form.Item;

const {Option} = Select;

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

@observer
class UploadQuestionForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    componentDidMount() {
        store.fetchPayImg();

    }

    sentQR = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                const IMG = JSON.stringify({img: values.pay_img});
                axios.get(
                    baseUrl + '/api/sent_websocket_message',
                    {
                        params: {
                            token: localStorage.customer_token,
                            id_to: localStorage.chat_id_to,
                            content: IMG,

                        }
                    })
                    .then((r) => {
                        if (r.data === 1) {
                            message.success('发送成功');
                            this.setState(prevState => ({
                                    showLeaveMessageInput: !(prevState.showLeaveMessageInput),
                                })
                            );
                            store.fetchChats();
                            store.tggModal();
                            store.tgglBtnFs();
                        } else {
                            message.error('发送失败，请稍后重试');
                        }
                    });
            }
        });


    };

    render() {
        const {getFieldDecorator} = this.props.form;

        return (
            <div className='choose-recv-qr'>
                <FormItem label='选择收款码'>
                    {getFieldDecorator('pay_img', {})(
                        <Select
                            style={{width: '100%', height: '80px'}}
                        >
                            {
                                store.getPayImg ?
                                    store.getPayImg.map(i => {
                                        console.log(i);
                                        return (
                                            <Option value={`${baseUrl}/${i.pay_img}`}>
                                                <img src={`${baseUrl}/${i.pay_img}`} width={''} height={'50px'}
                                                     alt={''}/>
                                            </Option>
                                        )
                                    })
                                    : null
                            }
                        </Select>
                    )}
                </FormItem>
                <Button
                    onClick={store.tggModalUpload}
                    style={{backgroundColor: 'lightgreen', color: 'white'}}
                >
                    <Icon type="plus"/>
                    上传收款码
                </Button>
                <Button
                >
                    添加
                </Button>
                <Button
                    type='primary'
                    onClick={this.sentQR.bind(this)}
                >
                    发送
                </Button>
                <Modal
                    visible={store.visibleModalRecvQRUpload}
                    onOk={store.tggModalUpload}
                    onCancel={store.tggModalUpload}
                    style={{textAlign: 'center', top: 20}}

                >
                    <UploadRecvQR/>
                </Modal>
            </div>
        )
    }
}

export const ChooseRecvQR = Form.create({})(UploadQuestionForm);
