import {configure, observable, computed, action, runInAction} from 'mobx';
import axios from 'axios'
import * as moment from "moment";
import {baseUrl} from "../../config";
// configure({
//     enforceActions: 'observed'
// });

class Store {
    // 技能
    @observable messages = [];

    @computed get messagesCount() {
        return this.messages.length;
    }

    @computed get messagesAll() {
        return this.messages;
    }

    @computed get messagesByUser() {
        const d = {};
        this.messages.map(i => {
            if (d[i.id_from] === undefined) {
                d[i.id_from] = [];
            }
            d[i.id_from].push(i)

        });
        return d;
    }

    @computed get workers() {
        return this.works.workers ? this.works.workers : [];
    }


    @action setMessages = (v) => {
        this.messages = v;
        console.log(this.messages.length)
    };

    @action
    async fetchMessages() {
        await axios.get(
            baseUrl + '/api/get_my_message',
            {
                params: {
                    token: localStorage.customer_token,
                    status: 1,
                }
            })
            .then(r => {

                this.setMessages(r.data);
            })
            .catch((err) => {
                console.log("in axios ", err)
            });
    }

}

export default new Store();