import React from 'react'
import './index.less'
import {Button, Icon, Modal, Radio} from "antd";
import axios from "axios";
import wx from "weixin-js-sdk";
import FrapSkill from "../../../cpns/FrapSkill";
import {observer} from "mobx-react";
import store from './store'
import {baseUrl} from "../../../config";
import moment from "moment";
import WorkCell from "../../../cpns/WorkListCell";
import {Link} from "react-router-dom";
import FrapStation from "../../../cpns/FrapStation";


@observer
class IndexSkill extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visibleCommunications: false,
            modalContent: '',
            showTags: false,
        }
    }

    componentDidMount() {

        axios.get(
            baseUrl + '/api/get_user_money',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((r) => {
                // 设置缓存
                localStorage.id_user = r.data.id;
                localStorage.nick_name = r.data.nickName;
                console.log(r.data)
            });
        document.title = "小蓝校园，发现更多好兼职～";
        store.fetchSkills();
        this.share()
    }

    convertTpl = (id) => {
        const d = {
            1: '快递代取',
            2: '电费代充',
            3: '电影票代买',
            4: '跑腿',
            5: '账号出租',
            6: '课程服务',
            7: '拍照',
            8: '修图',
            9: '化妆',
            10: '游戏陪玩',
            11: '电车出租',
            12: '司机',
            13: '服务员',
            14: '礼仪',
            15: '模特',
            16: '配音',
            17: '打印',
            18: '住宿',
            19: '商家',
            20: '微信群',
            21: '海大公众号',
            22: '二手',
            23: '招商',

        };
        return d[id]
    };
    share = () => {
        axios.get(baseUrl + '/api/get_jssdk',
            {
                params: {
                    url: window.location.href.split('#')[0]
                }
            }).then(res => {
            const params = {
                debug: false,
                appId: res.data.appId,
                timestamp: res.data.timestamp,
                nonceStr: res.data.nonceStr,
                signature: res.data.signature,
                jsApiList: [
                    'onMenuShareTimeline',
                    'onMenuShareAppMessage'
                ]
            };
            wx.config(params);
            wx.ready(function () {
                // config信息验证成功后会执行ready方法
                //通过ready接口处理成功验证
                wx.onMenuShareAppMessage({
                    // 分享给朋友
                    title: '小蓝校园，发现更多好兼职～', // 分享标题
                    desc: '找兼职，就上小蓝校园，100＋合作商家，靠谱！',
                    link: res.data.str, // 分享链接
                    imgUrl:
                        `${baseUrl}/storage/1v2F1byxddM3FGWFfCNQxmRGcw8DMTTCH0Q0rRHz.jpeg`, // 分享图标
                    type: "", // 分享类型,music、video或link，不填默认为link
                    dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
                wx.onMenuShareTimeline({
                    //分享朋友圈
                    title: '小蓝校园，发现更多好兼职～', // 分享标题
                    desc: '找兼职，就上小蓝校园，100＋合作商家，靠谱！',
                    link: res.data.str, // 分享链接
                    imgUrl: `${baseUrl}/storage/1v2F1byxddM3FGWFfCNQxmRGcw8DMTTCH0Q0rRHz.jpeg`, // 分享图标
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });

            });
        });
    };
    favoriteHandler = (id, hadCollected,receiver_id,skill_name) => {
        axios.get(
            baseUrl + '/api/collect_jineng',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_jineng: id,  //货号
                    method: hadCollected ? 'cancle' : 'none',    //取消赞
                    id_to:receiver_id,
                    nick_name:localStorage.nick_name,

                }
            })
            .then((Response) => {
                store.fetchSkills();
                // if (Response.data === 'success' || Response.data === 1) {
                //     // message.success('收藏成功');
                //     this.setState(prevState => ({
                //             collected: !(prevState.collected),
                //         })
                //     )
                // } else {
                //     message.error('失败，请稍后重试');
                // }
            });
        axios.get(
            baseUrl + '/api/sent_message',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_to: receiver_id,
                    content:`${localStorage.nick_name}收藏了您的${skill_name}`,
                }
            })
            .then((r) => {
                console.log(r.data);
            })
    };
    // 点赞
    likeHandler = (id, zaned,receiver_id,skill_name) => {
        const p = {
            token: localStorage.customer_token, //用户token
            id_jineng: id,  //货号
            id_to:receiver_id,
            nick_name:localStorage.nick_name,

        };
        if (zaned) {
            p.method = 'cancle'
        }
        axios.get(
            baseUrl + '/api/zan_jineng',
            {
                params: p
            })
            .then(() => {
                store.fetchSkills();

            })
        axios.get(
            baseUrl + '/api/sent_message',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    id_to: receiver_id,
                    content:`${localStorage.nick_name}赞了您的${skill_name}`,
                }
            })
            .then((r) => {
                console.log(r.data);
            })
    };
    jumpToSkillIndex = (id, event) => {
        console.log(JSON.stringify(event.target.className));
        // if (JSON.stringify(event.target.className) === JSON.stringify('ant-btn') || JSON.stringify(event.target.className) === JSON.stringify('skill-img')) {
        if (JSON.stringify(event.target.className) === JSON.stringify('right') || JSON.stringify(event.target.className) === JSON.stringify('bei_zhu')) {
            window.location.hash = `#/skillIndex/${id}`
        } else {
            return 0;

        }
    };

    renderSkillCells = () => {
        let cs = [];
        store.skills.map(i => {
            // console.log(i);
            // if (i.avilable) {
            if (i.status !== 0) {
                if (i.jineng_tpl === parseInt(this.state.tpl) || this.state.tpl === undefined) {
                    cs.push(
                        <div
                            className={i.avilable ? 'cell-skill' : ' not-avilable cell-skill'}
                            onClick={this.jumpToSkillIndex.bind(this, i.id)}
                        >

                            <div className='cell-skill-main'>
                                <div className='skill-img-container'>
                                    <img
                                        className='skill-img'
                                        src={`${baseUrl}/${i.content}`}
                                        alt=''
                                        onClick={() => {
                                            const WeixinJSBridge = window.WeixinJSBridge;
                                            WeixinJSBridge.invoke("imagePreview", {
                                                "urls": [`${baseUrl}/${i.content}`],
                                                "current": `${baseUrl}/${i.content}`
                                            });
                                        }}
                                    />
                                </div>
                                <div className='right'>
                                    <div className='bei_zhu'>{i.bei_zhu.slice(0, 25)}</div>
                                    <div className='find-him-btn'>
                                    </div>
                                </div>
                            </div>
                            <div className='cell-skill-footer'>
                                <div>
                                </div>
                                <span className='icon-favorite'
                                      onClick={this.favoriteHandler.bind(this, i.id, i.collected,i.id_openid,i.bei_zhu)}
                                >
                                    <Icon type="star"
                                          theme={i.collected ? 'filled' : ''}
                                          style={{fontSize: '1.5em', color: 'pink'}}
                                    />
                                    收藏
                                    {i.collected_count !== 0 ? i.collected_count : ''}

                                </span>
                                <span className='icon-favorite'
                                      onClick={this.likeHandler.bind(this, i.id, i.zan,i.id_openid,i.bei_zhu)}
                                >
                                    <Icon type="like"
                                          theme={i.zan ? 'filled' : ''}
                                          style={{fontSize: '1.5em', color: '#71ceff'}}
                                    />
                                    超赞
                                    {i.zan_count !== 0 ? i.zan_count : ''}

                                </span>
                                {/*<span className='c-time'>上线时间:{moment.unix(i.created_at).format("MM-DD HH:mm")}</span>*/}
                                <span className='tpl-tag'>{this.convertTpl(i.jineng_tpl)}</span>
                                <Button
                                    onClick={this.showCommunications.bind(this, i.id_communication)}
                                    className='show-btn'
                                >
                                    找他
                                </Button>
                            </div>
                        </div>
                    )
                }
            }
            // }
        });
        return cs
    };
    showCommunications = (id) => {
        console.log(id);
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    token: localStorage.customer_token,
                    method: 'solo_get',
                    id_communication: id,
                }
            })
            .then((r) => {
                this.setState({
                    visibleCommunications: true,
                    modalContent: <div>
                        <div>
                            <h2 className='name'>联系方式:{r.data.communications[0].phone}</h2>
                            <img src={`${baseUrl}/${r.data.communications[0].wx_png}`} alt=''
                                 className='avatar'
                                 width='100%'
                            />
                            <h5>提示：长按识别二维码添加微信</h5>
                        </div>
                    </div>,
                });
            });
        //点击量
        const zd_dict = {1:localStorage.zd};

        axios.get(
            baseUrl + '/api/get_jinengs',
            {
                params: {
                    token: localStorage.customer_token,
                    zd:JSON.stringify(zd_dict),


                }
            }
        )

    };
    handleChange = (vl) => {
        const v = vl.target.value;
        console.log(vl);
        console.log(v);
        this.setState({
            tpl: v === 'all' ? undefined : v,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    showMoreTags = () => {
        const prev = this.state.showTags;
        this.setState({
            showTags: !prev,
        })
    };

    render() {
        return (
            <div className="Skills">
                <FrapStation/>

                {/*<div className='palace-header'>*/}
                {/*    <div className='theme-header'>*/}
                {/*        /!*背景*!/*/}
                {/*    </div>*/}
                {/*    <div className='header-text'>*/}
                {/*        技能广场*/}
                {/*    </div>*/}
                {/*</div>*/}
                {/* 子路由导航 */}
                <div className='sticky'>
                    <div className='show-more-ctr'>
                        <span className='hot-tags'>热门标签</span>
                        <span
                            className='show-tags'
                            onClick={this.showMoreTags}
                        >
                                查看更多标签
                                <Icon type="down"/></span>

                    </div>
                    <div className='tpl-select'>
                        <Radio.Group defaultValue='all' onChange={this.handleChange}>
                            <Radio.Button value='all'>全部</Radio.Button>
                            <Radio.Button value="1">快递代取</Radio.Button>
                            <Radio.Button value="17">打印</Radio.Button>
                            <Radio.Button value="2">电费代充</Radio.Button>
                            <Radio.Button value="3">电影票代买</Radio.Button>
                            <Radio.Button value="6">课程服务</Radio.Button>
                            <Radio.Button value="18">住宿</Radio.Button>
                            <Radio.Button value="19">商家</Radio.Button>
                            {
                                this.state.showTags
                                    ?
                                    <div>
                                        <Radio.Button value="5">账号出租 </Radio.Button>
                                        <Radio.Button value="4">跑腿</Radio.Button>
                                        <Radio.Button value="7">拍照</Radio.Button>
                                        <Radio.Button value="8">修图</Radio.Button>
                                        <Radio.Button value="9">化妆</Radio.Button>
                                        <Radio.Button value="10">游戏陪玩</Radio.Button>
                                        <Radio.Button value="11">电车出租</Radio.Button>
                                        <Radio.Button value="12">司机</Radio.Button>
                                        <Radio.Button value="13">服务员</Radio.Button>
                                        <Radio.Button value="14">礼仪</Radio.Button>
                                        <Radio.Button value="15">模特</Radio.Button>
                                        <Radio.Button value="16">配音</Radio.Button>
                                        <Radio.Button value="20">微信群</Radio.Button>
                                        <Radio.Button value="21">海大公众号</Radio.Button>

                                        <Radio.Button value="22">二手</Radio.Button>
                                        <Radio.Button value="23">招商</Radio.Button>
                                    </div>
                                    : null
                            }
                        </Radio.Group>
                    </div>
                </div>
                <div className='cells-container-skills'>
                    {
                        this.renderSkillCells()
                    }
                </div>
                <Modal
                    visible={this.state.visibleCommunications}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    {this.state.modalContent}
                </Modal>
                <FrapSkill/>
            </div>

        )
    }
}

export default IndexSkill;