import React from 'react'
import {Button, Icon, Modal, Select} from 'antd'
import {Link} from 'react-router-dom'
import './index.less'

const {Option} = Select;

export default class FrapStation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visiblePostLinks: false,
        }
    }

    handleChange = (value) => {
        console.log(`selected ${value}`);
        localStorage.zd = value;
        window.location.reload();


    };

    render() {
        return (
            <div className='frap-station'>
                <Icon type="environment"/>
                选择站点：
                <Select
                    defaultValue={localStorage.zd!==undefined?localStorage.zd:'1'}
                    style={{width: 120}}
                    onChange={this.handleChange}
                >
                    <Option value="1">广东海洋大学主校区</Option>
                    <Option value="2">广东海洋大学霞山校区</Option>
                    <Option value="3">广东海洋大学海滨校区</Option>
                </Select>
            </div>
        )
    }
}