import React from 'react'
import './index.less'
import {Icon, message, notification} from 'antd';
import axios from "axios";
import {baseUrl} from "../../config";
import {observer} from "mobx-react";
import store from "./store";
import {withRouter} from "react-router-dom";


const staticUrl = baseUrl;

@observer
class Messages extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // 防止重复点击
            isClick: true,
            // baseUrl:baseUrl,
        };
        store.fetchMessages();
    }

    renderMessages = () => {
        const ms = store.messagesByUser;
        console.log(ms);
        const cs = [];
        for (const m in ms) {
            const v = ms[m];
            v.map(i => {
                cs.push()
            });
            console.log(v)
        }
    };
    goBack = () => {
        this.props.history.goBack()
    };

    render() {
        return (
            <div className='ms-container'>
                <div className='go-back'>
                <span onClick={this.goBack}>
                        <Icon type="left"/>
                        <span>返回</span>
                    </span>
                </div>
                {store.messagesAll.map(i => {
                    return (
                        <div className='m-cell'>
                            <span className='m-content'>
                            {i.content}
                            </span>
                        </div>
                    )
                })}
                {this.renderMessages()}
            </div>
        )
    }
}
export default withRouter(Messages);